 ")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Cultura ambiental"}},[_c('div',{staticClass:"col-lg-5 my-3"},[_c('img',{staticClass:"img-mov",attrs:{"src":require("@/assets/curso/temas/104.png")}})]),_c('div',{staticClass:"col-lg-7 my-3"},[_c('p',[_vm._v("En los procesos de promotoría ambiental, interesa, de manera relevante, medir el nivel de cultura ambiental a partir de los valores relacionados con la temática ambiental ejecutada, como la protección del medio ambiente, la prevención de la contaminación, el respeto a la tierra. Igualmente, es posible incluir dentro de la evaluación el análisis frente a las creencias, como variables que anteceden y predicen actitudes y comportamientos en pro del medio ambiente. ")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Actitudes ambientales adquiridas o potenciadas"}},[_c('div',{staticClass:"col-lg-5 my-3"},[_c('img',{staticClass:"img-mov",attrs:{"src":require("@/assets/curso/temas/105.png")}})]),_c('div',{staticClass:"col-lg-7 my-3"},[_c('p',[_vm._v("Otro elemento para incluir en la evaluación son las actitudes ambientales, ya que tienen una gran influencia sobre el comportamiento cuando otros factores no impiden que este se lleve a cabo, sobre todo en lo referente a los comportamientos individuales de consumo y de participación ambiental. Y finalmente, incluir las conductas pro-ambientales, como grado de conocimiento de las causas y efectos de la problemática ambiental, motivación, capacidad de generar cambios y el grado de convencimiento de que su acción tendrá efectividad (Miranda, 2013). ")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Mejora continua"}},[_c('div',{staticClass:"col-lg-5 my-3"},[_c('img',{staticClass:"img-mov",attrs:{"src":require("@/assets/curso/temas/106.png")}})]),_c('div',{staticClass:"col-lg-7 my-3"},[_c('p',[_vm._v("Es importante ver la evaluación como parte del proceso permanente de mejora continua; como una actividad recurrente para mejorar el desempeño y no como un medio coercitivo (Organización Internacional de Normalización). De allí que esta sirva en todas las etapas para identificar las oportunidades de mejora y poder establecer las acciones preventivas y correctivas que de ella se deriven, permitiendo aprender de los problemas y actuar para que no se vuelvan a presentar (González, 2005). ")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Fortalecimiento de las áreas del proceso"}},[_c('div',{staticClass:"col-lg-5 my-3"},[_c('img',{staticClass:"img-mov",attrs:{"src":require("@/assets/curso/temas/107.png")}})]),_c('div',{staticClass:"col-lg-7 my-3"},[_c('p',[_vm._v("Entonces, se deben identificar las áreas con problemas, las disposiciones para mitigar impactos, definir las responsabilidades para investigar y actuar, así como la metodología para identificar la causalidad y la solución de problemas identificados (Toro, 2017). ")])])])])],1)])])],1)}
